<template>
  <div>
    <ul class="tag" v-if="showTag">
      <router-link to="/views/collegeFacilities/area">
        <li :class="{ active: state == 1 }" @click="selectTag(1)">实验场地</li>
      </router-link>

      <router-link to="/views/collegeFacilities/device">
        <li :class="{ active: state == 2 }" @click="selectTag(2)">研究设备</li>
      </router-link>
    </ul>
    <div v-if="showTag" class="tagNull"></div>
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'collegeFacilities',
  data () {
    return {
      state: 1,
      tagsUrl: [
        '/views/collegeFacilities/area',
        '/views/collegeFacilities/device'
      ],
      showTag: false
    }
  },
  methods: {
    selectTag (state) {
      if (state == this.state) {
        return
      }
      this.state = state
    }
  },
  created () {
    const arrIndex = $.inArray(this.$route.path, this.tagsUrl)
    if (arrIndex > -1) {
      this.showTag = true
      this.state = arrIndex + 1
    }
  }
}
</script>



